
import {
    IonApp,
    IonContent,
    IonIcon,
    IonItem,
    IonLabel,
    IonList,
    IonListHeader,
    IonMenu,
    IonMenuToggle,
    IonNote,
    IonRouterOutlet,
    IonSplitPane,
    IonButton,
} from '@ionic/vue'
import { defineComponent, ref } from 'vue'
import { useRoute } from 'vue-router'
import {
    archiveOutline,
    archiveSharp,
    bookmarkOutline,
    bookmarkSharp,
    heartOutline,
    heartSharp,
    mailOutline,
    mailSharp,
    paperPlaneOutline,
    paperPlaneSharp,
    trashOutline,
    trashSharp,
    warningOutline,
    warningSharp,
} from 'ionicons/icons'
import LoginPage from './views/LoginPage.vue'

export default defineComponent({
    name: 'App',
    components: {
        IonApp,
        IonContent,
        IonIcon,
        IonItem,
        IonLabel,
        IonList,
        IonListHeader,
        IonMenu,
        IonMenuToggle,
        IonNote,
        IonRouterOutlet,
        IonSplitPane,
        LoginPage,
        IonButton,
    },
    methods: {
        logout() {
            localStorage.removeItem('userToken')
            window.location.href = '/'
        },
    },
    setup() {
        const selectedIndex = ref(0)
        const appPages = [
            {
                title: 'Expenses',
                url: '/folder/Expenses',
                iosIcon: mailOutline,
                mdIcon: mailSharp,
            },
            {
                title: 'Incomes',
                url: '/folder/Incomes',
                iosIcon: mailOutline,
                mdIcon: mailSharp,
            },
        ]

        const path = window.location.pathname.split('folder/')[1]
        if (path !== undefined) {
            selectedIndex.value = appPages.findIndex(
                (page) => page.title.toLowerCase() === path.toLowerCase()
            )
        }

        const route = useRoute()

        return {
            selectedIndex,
            appPages,
            archiveOutline,
            archiveSharp,
            bookmarkOutline,
            bookmarkSharp,
            heartOutline,
            heartSharp,
            mailOutline,
            mailSharp,
            paperPlaneOutline,
            paperPlaneSharp,
            trashOutline,
            trashSharp,
            warningOutline,
            warningSharp,
            isSelected: (url: string) => (url === route.path ? 'selected' : ''),
        }
    },
    data() {
        return {
            appName: 'Finance Monke',
            username: 'Maxime Chaussé',
            userToken: localStorage.getItem('userToken'),
        }
    },
})
